@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400);
body {
  height: 100vh;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 200;
}

.block {
  display: grid;
  grid-template-columns: calc(25vh + 220px) auto;
  height: 100vh;
}

.title {
  text-align: center;
}

.title h2 {
  text-align: center;
  font-size: 50px;
  margin-bottom: 0;
}

.title img{
  width: 100px;
}


