.btn {
  background-color: #d31328;
  color: white;
  padding: 15px 20px;
  border: none;
  text-decoration: none;
  margin-right: 20px;
  cursor: pointer;
}

#home {
  align-self: center;
  text-align: right;
  margin-right: 150px;
  height: 100vh;
  display: grid;
}

#home div {
  -webkit-align-self: center;
          align-self: center;
}

#home h1{
  margin: 0;
  font-size: 80px;
  font-weight: 300;
}

#home h4{
  font-size: 40px;
  margin: 0 0 30px 0;
  font-weight: 100;
}

#home .btn {
  margin: 0;
  font-size: 25px;
}

.logo {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 45vh;
}

.projects .block{
  grid-template-columns: auto auto;
  height: auto;
}

/* Menu style */

.circle {
  border-radius: 50%;
  position: fixed;
  border: solid 30px #d31328;
  top: -15px;
  left: -80vh;
  width: calc(100vh - 30px);
  height: calc(100vh - 30px);
}

.nav-item {
  z-index: 200;
  position: fixed;
  text-decoration: none;
  display: grid;
  grid-template-columns: auto auto;
}


.nav-item .text{
  display: none;
}

.nav-item:hover .text{
  color: #000000;
  display: inline;
}

.nav-item:hover .text, .active .text{
  color: #000000;
  display: grid;
  text-align: -webkit-center;
  position: relative;
  left: 100px;
}

.text p{
  margin-bottom: 0;
  font-size: 30px;  
}


.nav-count-1 {
  top: calc(20vh - 30px); 
}

.nav-count-2{
  top: calc(35vh - 30px); 
  left: 8vh;  
}

.nav-count-3 {
  top: calc(50vh - 30px); 
  left: 12vh;
}

.nav-count-4 {
  top: calc(65vh - 30px); 
  left: 8vh;
}

.nav-count-5 {
  top: calc(80vh - 30px); 
  left: 0;
}



